import React from 'react';
import {Link} from 'react-router-dom';

const Header = props=>{
    return(
        <div className="jumbotron py-3 " id="container-header">
            <div className="row">
                <div className="col-12">
                    <Link to={'/'}>
                        <h1 className="text-center">Todo List</h1>
                    </Link>
                </div>
            </div>
        </div>
    )
}
export default Header;