import React from 'react';

const Prioridad = props=>{

    const prioridad = props.prioridad;
    let clase='';
    switch (prioridad){
        case 'Alta': clase = 'badge badge-pill badge-danger'; break;
        case 'Media': clase = 'badge badge-pill badge-warning'; break;
        case 'Baja': clase = 'badge badge-pill badge-success'; break;
        default: return;
    }
        

    return( 
        <span className={clase}>{prioridad}</span>
    );
}
export default Prioridad;