import React,{Component} from 'react';
import Tarea from './Tarea.jsx';
import {Link} from 'react-router-dom';
class Tareas extends Component{

    render(){
        const tareas = this.props.tareas;
        return(
            <div className="container py-4" id="container-tareas">
                <div className="row  ">
                    <div className="col-12 d-flex">
                        <Link to={'/creartarea'} className="btn btn-info  ml-auto">Crear Tarea</Link>
                    </div>
                    <div className="col-12">
                        <h3>Nuevas Tareas</h3>
                    </div>
                    {Object.keys(tareas).map(key=>{
                        return(<Tarea tarea={tareas[key]} key={key} pos={key} eliminarTarea={this.props.eliminarTarea}/>);
                    })}
                </div>
            </div>
        );
    }
}

export default Tareas;