import React,{Component} from 'react';
import {BrowserRouter,Route,Switch} from 'react-router-dom';


import Header from './Header.jsx';
import Tareas from './Tareas.jsx';
import axios from 'axios';
import CrearTarea from './CrearTarea.jsx';

class Router extends Component{
    state={
        tareas:[]
      }
      crearTarea=nuevaTarea=>{
        const {nombre,prioridad} = nuevaTarea;
          axios.post('/creartarea', {
            nombre,
            prioridad
          })
          .then( (response)=> {
            const tareas = [...this.state.tareas];
            tareas.push(nuevaTarea);
            this.setState({tareas});
          })
          .catch( (error)=> {
            console.log(error)
          });
      }
      eliminarTarea=pos=>{
        axios.post('/creartarear',{pos})
                                        .then(response=>{
                                          let tareas = [...this.state.tareas];
                                          tareas.splice(pos, 1);
                                          this.setState({tareas});
                                        })
                                        .catch(err=>console.log(err))
      }

      
      componentDidMount(){
        axios.get('/list')
                          .then(response=>{
                            this.setState({tareas:response.data})
                          })
      }
    render(){
        return(
            <BrowserRouter>
                <Header/>
                <Switch>
                    <Route exact path="/" render={()=>{
                        return(
                            <Tareas tareas={this.state.tareas}
                                    eliminarTarea={this.eliminarTarea}
                            />
                        )
                    }}>
                    </Route>
                    <Route exact path="/creartarea" render={()=>{
                        return(
                            <CrearTarea 
                                crearTarea={this.crearTarea}
                            />
                        )
                    }}>

                    </Route>
                </Switch>
            </BrowserRouter>
        );
    }
}
export default Router;