import React,{Component} from 'react';
import Prioridad from './Prioridad.jsx';
class Tarea extends Component{
    infoTarea=()=>{
        this.props.eliminarTarea(this.props.pos);
    }
    
    render(){
        const tarea = this.props.tarea;
        return(
            <div className="col-12" id="container-tarea">
                <p className="d-flex justify-content-between m-3"> 
                    <span id='tarea-nombre '><button className="btn btn-info" onClick={this.infoTarea}>x</button> {tarea.nombre}</span>
                    <Prioridad
                        prioridad={tarea.prioridad}
                    />
                </p>
            </div>
        )
    }
}
export default Tarea;