import React,{Component} from 'react';
import swal from 'sweetalert';


class CrearTarea extends Component{
    nombreRef = React.createRef();
    prioridadRef = React.createRef();
    obtenerDatos=e=>{
        e.preventDefault();
        const nombre = this.nombreRef.current.value;
        const prioridad = this.prioridadRef.current.value;

        const data = {
            nombre,prioridad
        }
    
        this.props.crearTarea(data);

        swal({
            title: "Tarea Creada!!!",
            icon: "success",
            button: "Continuar",
        });

        e.target.reset();
    }
    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col">
                        <form className="bg-white p-3" onSubmit={this.obtenerDatos}>
                            <h2>Crear Tarea</h2>
                            <div className="form-group">
                                <label htmlFor="input-nombre">Nombre de la tarea</label>
                                <input className="form-control" type="text" id="input-nombre" ref={this.nombreRef} />
                            </div>
                            <div className="form-group">
                                <label htmlFor="input-prioridad">Nombre de la tarea</label>
                                <select id="input-prioridad" className="form-control" ref={this.prioridadRef}>
                                    <option defaultValue>Alta</option>
                                    <option>Baja</option>
                                    <option>Media</option>
                                </select>
                            </div>
                            <button className="btn btn-info" type="submit" >Crear Tarea</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}
export default CrearTarea;